.callback__checkbox-wrapper {
  width: 260px;
  display: flex;
  align-items: center;
}
.callback__checkbox-wrapper label {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
  padding-left: 26px;
}

.form input,
.form textarea {
  margin: 0;
  padding: 15px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #000000;
  border: 0;
}
.form input::placeholder,
.form textarea::placeholder {
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
}
.form input {
  margin-bottom: 20px;
}

.accept label {
  position: relative;
  cursor: pointer;
}
.accept label::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #ffffff;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 0);
}

.accept input:checked + label::after {
  content: "";
  position: absolute;
  background-image: url("../img/svg/check.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  left: 3px;
  width: 12px;
  height: 8px;
  top: 50%;
  transform: translateY(-50%);
}

